html,
body {
  margin: 0;
  padding: 0;
  background-color: #fff;
  background: url("../public/againagain.png") no-repeat right bottom fixed;
  background-size: 500px;
  text-align: center;
  /* padding-bottom: 100px; */
  overflow-x: hidden;
  touch-action: manipulation;
  user-select: none;
  min-height: 100%;
}

.app {
  display: inline-block;
  max-width: 400px;
  min-width: 400px;
  width: 100%;
  text-align: left;
}

.section {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  box-shadow: inset -1px -1px 1px rgba(0, 0, 0, 0.2),
    0px 2px 10px -5px rgba(0, 0, 0, 0.3);
  margin: 10px 10px 10px 10px;
  background: rgba(255, 255, 255, 0.96);
  padding: 15px;
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
}

.habits {
  margin-bottom: 20px;
}

.app-editing .habit-name {
  background: rgba(0, 0, 0, 0.05);
}

.app-editing .project {
  animation: wiggleAnimation 0.3s infinite;
  cursor: move;
}

.app-editing .project .project-title {
  cursor: move;
}

.app-editing .habit {
  animation: wiggleAnimation 0.3s infinite;
}

.project {
  border: 1px solid rgb(221, 221, 221);
  /* box-shadow: inset -1px -1px 1px rgba(0, 0, 0, 0.2),
    inset 0px 3px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5), */
  box-shadow: 0px 2px 10px -5px rgba(0, 0, 0, 0.3);

  background: linear-gradient(
    170deg,
    rgba(204, 229, 255, 0.9),
    rgba(255, 255, 255, 0.9) 25%,
    rgba(255, 255, 255, 0.7)
  );
}

.project-arrow {
  margin-right: 10px;
}

h1 {
  margin: 20px 50px;
}

.habit {
  display: flex;
  align-items: center;
  text-align: center;
  flex-flow: column;
  width: 100px;
  cursor: pointer;
  user-select: none;
}

.habit-icon {
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  width: 75px;
  height: 75px;
  border-radius: 50%;
  box-shadow: 0px 0px 0px 4px rgba(40, 200, 40, 0.5),
    inset -1px -1px 1px rgba(0, 0, 0, 0.5),
    inset 0px 3px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5),
    1px 3px 5px rgba(0, 0, 0, 0.1);
}

.habit:hover {
  opacity: 0.8;
}

.habit:active .habit-icon {
  box-shadow: inset 1px 1px 4px rgba(0, 0, 0, 0.5),
    inset 0px -1px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5),
    1px 3px 5px rgba(0, 0, 0, 0.1), inset 0px 500px 2px -1px rgba(0, 0, 0, 0.1);
}

.habit.habit-done .habit-icon {
  box-shadow: inset 2px 3px 5px rgba(0, 0, 0, 0.5),
    inset 0px -1px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5),
    1px 3px 5px rgba(0, 0, 0, 0.1), inset 0px 500px 2px -1px rgba(0, 0, 0, 0.1);
}

.habit-icon-inner {
  font-size: 3em;
  width: 100%;
  text-align: center;
}

.habit-name {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1em;
  margin-top: 10px;
  color: rgb(63, 63, 63);
  min-height: 1em;
}

.habit-progress {
  display: flex;
  justify-content: center;
  gap: 0px;
  align-items: center;
  position: absolute;
  background: rgb(255, 255, 255);
  height: 20px;
  width: 100%;
  opacity: 0;
  transition: opacity 0.1s;
}

.habit-progress.habit-progress-visible {
  opacity: 1;
}

.habit-progress-block {
  border: 1px solid rgba(0, 0, 0, 0.1);
  pointer-events: none;
  touch-action: none;
}

.habit-progress-done-today {
  display: flex;
  /* border-radius: 2px; */
  /* border: 2px solid rgb(218, 218, 218, 0); */
  background: rgb(255, 255, 255, 0);
  transform: scale(10);
  width: 10px;
  height: 10px;
  transition: background-color 1s, border 1s, transform 1.01s;
}

.habit-progress.habit-progress-visible .habit-progress-done-today {
  /* border: 2px solid rgb(87, 175, 87); */
  box-shadow: 0px 1px 9px rgba(0, 0, 0, 0.2);
  background: rgb(148, 197, 148);
  transform: scale(1);
}

.habit-progress-done,
.habit-progress-skip {
  transform: translateX(100%);
  transition: transform 0.5s;
}

.habit-progress.habit-progress-visible .habit-progress-done,
.habit-progress.habit-progress-visible .habit-progress-skip {
  transform: translateX(0%);
}

.habit-progress-done {
  display: flex;
  /* border-radius: 2px; */
  /* border: 2px solid rgb(77, 157, 77); */
  background: rgb(148, 197, 148);
  width: 10px;
  height: 10px;
}

.habit-progress-skip {
  display: flex;
  /* border-radius: 2px; */
  /* border: 2px solid rgb(218, 218, 218); */
  background: rgb(255, 220, 220);
  width: 10px;
  height: 10px;
}

.habit-name-inner {
  width: 90px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@keyframes markHabitDone {
  0% {
    outline: 0px solid rgba(0, 255, 0, 0.2);
    /* box-shadow: 0px 0px 10px rgba(0, 255 , 0, 1.0); */
    opacity: 0;
  }
  50% {
    outline: 20px solid rgba(0, 255, 0, 0);
    /* box-shadow: 0px 0px 10px rgba(0, 255 , 0, 0.8); */
    opacity: 0.8;
  }
  100% {
    outline: 20px solid rgba(0, 255, 0, 0);
    /* box-shadow: 0px 0px 10px rgba(0, 255 , 0, 0.0); */
    opacity: 1;
  }
}

.project h1 {
  margin: 0;
  padding: 0;
  color: rgb(70, 70, 70);
  font-size: 1.5em;
  cursor: pointer;
}

.project-header {
  display: block;
  width: 100%;
}

.project-tasks {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  /* align-items: stretch; */
  /* align-content: stretch; */
  width: 100%;
  gap: 20px;
}

.project-tasks-todo .project-tasks-title {
  color: rgb(100, 100, 100);
}

.project-tasks-doing .project-tasks-title {
  color: rgb(92, 121, 174);
}

.project-tasks-done .project-tasks-title {
  color: rgb(62, 134, 101);
}

.project-tasks-trash .project-tasks-title {
  color: rgb(31, 31, 31);
}

.project-tasks-waiting .project-tasks-title {
  color: rgb(116, 117, 75);
}

.project-tasks-title,
.project-tasks-title {
  padding: 5px;
  font-size: 1em;
  font-weight: bold;
}

.project-tasks-todo,
.project-tasks-doing,
.project-tasks-waiting,
.project-tasks-done,
.project-tasks-trash {
  flex: 10 1 0px;
  box-shadow: inset -1px -1px 1px rgba(0, 0, 0, 0.1),
    inset 0px 3px 0px -1px rgba(255, 255, 255, 1);
}

.project-tasks-done {
  background: rgb(221, 255, 239);
  flex: 0 0;
  /* align-self: flex-start; */
}

.project-tasks-trash {
  background: rgb(241, 241, 241);
  flex: 0 0;
  /* align-self: flex-start; */
}

.project-tasks-todo,
.project-tasks-doing,
.project-tasks-waiting,
.project-tasks-done,
.project-tasks-trash,
.project-tasks-add {
  display: flex;
  margin: -5px;
  padding: 5px;
  border-radius: 10px;
  display: flex;
  flex-flow: column;
  gap: 5px;
  /* border: 2px solid rgba(0, 0, 0, 0.1); */
}

.project-tasks-other {
  display: flex;
  flex-direction: column;
  gap: 20px;
  flex: 10 1 0px;
}

.project-tasks-todo {
  background: rgb(249 249 249);
  flex-grow: 0;
}

.project-tasks-doing {
  background: rgb(235, 242, 255);
  flex-grow: 0;
}

.project-tasks-waiting {
  background: rgb(255 246 226);
  flex-grow: 0;
}

.task {
  cursor: pointer;
  box-shadow: inset -1px -1px 1px rgba(0, 0, 0, 0.2),
    inset 0px 3px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5),
    0px 2px 10px -5px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background: rgb(255, 255, 255);
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: 10px;
  user-select: none;
  touch-action: none;
  pointer-events: none;
}

.task-wrapper {
  user-select: none;
}

.project-tasks-nibs {
  display: flex;
  flex: 1 1 0px;
  flex-grow: 0;
  flex-direction: column;
  align-items: start;
  gap: 20px;
}

.project-tasks-nibs .project-tasks-done {
  cursor: pointer;
  display: block;
  background: rgb(221, 255, 239);
  outline: 1px dashed rgba(0, 0, 0, 0.1);
  box-shadow: none;
  padding: 5px;
  width: 100%;
  flex-grow: 0;
}

.project-tasks-nibs .project-tasks-waiting .project-tasks-title {
  float: left;
  display: block;
  width: fit-content;
  color: rgb(89, 93, 61);
}

.project-tasks-nibs .project-tasks-waiting {
  cursor: pointer;
  display: block;
  background: rgb(255, 253, 221);
  outline: 1px dashed rgba(0, 0, 0, 0.1);
  box-shadow: none;
  padding: 5px;
  width: 100%;
  flex-grow: 0;
}

.project-tasks-nibs .project-tasks-done .project-tasks-title {
  float: left;
  display: block;
  width: fit-content;
  color: rgb(61, 93, 71);
}

.project-tasks-nibs .project-tasks-trash {
  cursor: pointer;
  display: block;
  background: rgb(241, 241, 241);
  outline: 1px dashed rgba(0, 0, 0, 0.1);
  box-shadow: none;
  padding: 5px;
  width: 100%;
  flex-grow: 0;
}

.project-tasks-nibs .project-tasks-trash .project-tasks-title {
  float: left;
  display: block;
  width: fit-content;
  color: rgb(61, 93, 71);
}

.project-tasks-add {
  cursor: pointer;
  display: block;
  outline: 1px solid rgb(0, 0, 0, 0.2);
  background: linear-gradient(
    to bottom,
    rgb(243, 243, 243),
    rgb(221, 221, 221)
  );
  box-shadow: inset -1px -1px 1px rgba(0, 0, 0, 0.2),
    inset 0px 3px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5),
    0px 2px 10px -5px rgba(0, 0, 0, 0.3);
  padding: 5px;
  width: 100%;
  flex-grow: 0;
  text-align: center;
}

.project-tasks-add .project-tasks-title {
  margin: 0;
  padding: 0;
}

.project-delete {
  border-radius: 10px;
  cursor: pointer;
  display: block;
  outline: 1px solid rgb(0, 0, 0, 0.2);
  background: linear-gradient(
    to bottom,
    rgb(243, 243, 243),
    rgb(221, 221, 221)
  );
  box-shadow: inset -1px -1px 1px rgba(0, 0, 0, 0.2),
    inset 0px 3px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5),
    0px 2px 10px -5px rgba(0, 0, 0, 0.3);
  padding: 10px 5px;
  width: 100%;
  flex-grow: 0;
  text-align: center;
}

.project-delete .project-tasks-title {
  margin: 0;
  padding: 0;
}

.project-delete:hover {
  opacity: 0.7;
}

.project-delete:active {
  box-shadow: inset 1px 1px 4px rgba(0, 0, 0, 0.5),
    inset 0px -1px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5),
    1px 3px 5px rgba(0, 0, 0, 0.1), inset 0px 500px 2px -1px rgba(0, 0, 0, 0.1);
}

.habit-delete,
.habit-icon-btn {
  border-radius: 10px;
  cursor: pointer;
  display: block;
  outline: 1px solid rgb(0, 0, 0, 0.2);
  background: linear-gradient(
    to bottom,
    rgb(243, 243, 243),
    rgb(221, 221, 221)
  );
  box-shadow: inset -1px -1px 1px rgba(0, 0, 0, 0.2),
    inset 0px 3px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5),
    0px 2px 10px -5px rgba(0, 0, 0, 0.3);
  padding: 10px 10px;
  width: 20px;
  height: 20px;
  font-size: 0.7em;
  border-radius: 100%;
  flex-grow: 0;
  text-align: center;
  position: absolute;
  top: -5px;
  left: -5px;
  z-index: 500;
  transition: opacity 0.15s;
  opacity: 1;
}

.habit-icon-btn.hidden,
.habit-delete.hidden {
  opacity: 0;
  pointer-events: none;
}

.habit-icon-btn {
  left: 50px;
}

.habit-delete:hover,
.habit-icon-btn:hover {
  opacity: 0.7;
}

.habit-delete:active,
.habit-icon-btn:active {
  box-shadow: inset 1px 1px 4px rgba(0, 0, 0, 0.5),
    inset 0px -1px 2px -1px rgba(255, 255, 255, 1),
    inset 0px 40px 50px -20px rgba(255, 255, 255, 0.5),
    1px 3px 5px rgba(0, 0, 0, 0.1), inset 0px 500px 2px -1px rgba(0, 0, 0, 0.1);
}

.habit:hover {
  opacity: 0.8;
}

.project-tasks-add:hover {
  opacity: 0.8;
}

.project-tasks-add:active {
  box-shadow: inset 1px 1px 4px rgba(0, 0, 0, 0.5),
    inset 0px -1px 2px -1px rgba(255, 255, 255, 1),
