}

@keyframes wiggleAnimation {
  0% {
    transform: rotate(0deg) translate(0, 0);
  }
  25% {
    transform: rotate(0.5deg) translate(2px, 0);
  }
  50% {
    transform: rotate(-0.5deg) translate(-2px, 2px);
  }
  75% {
    transform: rotate(0.5deg) translate(2px, 2px);
  }
  100% {
    transform: rotate(0deg) translate(0px, -2px);
  }
}

